import { createUseStyles } from 'react-jss';
import sizes from './Sizes'


export default createUseStyles({
    MainPage: {
        boxSizing: 'border-box',
        display: 'flex',
        borderRadius: '0 0 6px 0',
        overflowY: 'visible',
        flexWrap: 'wrap',
        backgroundColor: '#fafad2',
        fontSize: '1.5rem',
        height: '700px',
        textIndent: '50px',
        padding: '0 25px',
        width: '100%',
        paddingTop: '20px',
        minHeight: '600px',
        [sizes.down('sm')]: {
    
            display: 'none',

            },
    },
    sp: {
        fontSize: '2rem'
    }
})

