import React, { Component } from 'react'
import styles from '../styles/CanvasStyles'


export default class path extends Component {
    constructor(props){
        super(props);
        this.state = {PATH : '', vertex: '', edge: ''}
      }

      adjacencyList = {}
      values = []

      componentDidUpdate(prevProps){
          if (prevProps.distance !== this.props.distance){
              this.handleAddEdge();
          }
      }

      enqueue(val, priority) {
        this.values.push({val, priority});
        this.values.sort((a, b) => a.priority - b.priority);
    }

      addVertex(vertex){
        if(!this.adjacencyList[vertex]){
        this.adjacencyList[vertex] = [];
            }
        }

      addEdge(vertex1,vertex2, weight){
            this.adjacencyList[vertex1].push({node:vertex2,weight});
            this.adjacencyList[vertex2].push({node:vertex1, weight});
        }

      handleAddVertex=()=>{
            this.addVertex(this.props.num);

        }

      handleAddEdge=()=>{
            const {start, finish, distance} = this.props
            this.addVertex(start)
            this.addVertex(finish)
            this.addEdge(start, finish, distance);
        }


      dequeue() {
        return this.values.shift();
      }

      handleChangeStartDijskstra = (evt) => {
        this.setState({...this.state, startD: evt.target.value})
    }

    handleChangeFinishDijkstra = (evt) => {
        this.setState({...this.state, finishD: evt.target.value})
    }

    handleSubmitDijkstra=(evt)=>{
        const {startD, finishD} = this.state;
           if(startD &&  finishD){
            this.Dijkstra(startD, finishD)
           }
        evt.preventDefault();
    }

      handleDelete = () => {
        this.adjacencyList = {}
        this.values = []
        this.props.deleteCities()
        this.setState({finishD: '', startD: '', PATH: ''})
        
    }

      Dijkstra(start, finish){
        const distances = {};
        const previous = {};
        let path = [] //to return at end
        let smallest;
        //build up initial state, start will have value 0, the rest Infinity. All previous nodes 
        //will be null
        for(let vertex in this.adjacencyList){
            if(vertex === start){
                distances[vertex] = 0;
                this.enqueue(vertex, 0);
            } else {
                distances[vertex] = Infinity;
                this.enqueue(vertex, Infinity);
                
            }
            previous[vertex] = null;
    
        }
        // as long as there is something to visit
        while(this.values.length){

            smallest = this.dequeue().val;
            if(smallest === finish){
                //WE ARE DONE
                //BUILD UP PATH TO RETURN AT END
                while(previous[smallest]){
                    path.push(smallest);
                    smallest = previous[smallest];
                }
                break;
            } 
            if(smallest || distances[smallest] !== Infinity){
                for(let neighbor in this.adjacencyList[smallest]){

                    //find neighboring node
                    let nextNode = this.adjacencyList[smallest][neighbor];

                    //calculate new distance to neighboring node, current node 
                    //is candidate for being part of shortest path to neighbor
                    let candidate = distances[smallest] + nextNode.weight;
                    let nextNeighbor = nextNode.node;

                    //Check if the new distance to 'x' is less then the current one
                    if(candidate < distances[nextNeighbor]){        
                        //updating new smallest distance to neighbor
                        distances[nextNeighbor] = candidate;
                        //updating previous - How we got to neighbor
                        previous[nextNeighbor] = smallest;
                        //enqueue in priority queue with new priority
                        this.enqueue(nextNeighbor, candidate);
                    
                    }
                }
                console.log(this.adjacencyList)
            }
        }
        const paths = path.concat(smallest).reverse()
        this.setState({...this.state, PATH: paths})
    }
    

    render() {
        const {startD, finishD, PATH } = this.state;
        
        return (
            <div>
                <form style={styles.form} onSubmit={this.handleSubmitDijkstra}  >
                    <p style={styles.txtColor}>Type in starting and finishing point</p>
                    <input type='text' style={styles.input} value={startD} onChange={this.handleChangeStartDijskstra} />
                    <input type='text' style={styles.input} value={finishD} onChange={this.handleChangeFinishDijkstra}/>
                    <input type='submit' style={styles.button} value='Calculate' /><br></br>
                    <button onClick={this.handleDelete} style={styles.restart}>Restart Application</button>
                    <h4 style={styles.txtColor}>{PATH && `The shortest path is: ${PATH}`}</h4>
                </form>
            </div>
        )
    }
}
