import { createUseStyles } from 'react-jss'

export default createUseStyles({
    navbar: {
        display:'flex',
        borderRadius: '6px 6px 0 0',
        overflow: 'visible',
        paddingLeft: 'auto',
        marginTop: '55px',
        minHeight: '90px',
        width: '100%',
        flexBasis: 'max-content',
        backgroundColor: '#f5fafa',
        fontSize: '1rem',
        justifyContent: 'flex-end',
        whiteSpace: 'wrap',
        textDecoration: 'none',
        '& a':{
            textDecoration: 'none',
            padding: '15px'
        }
    },
    

    sp: {
        fontSize: '2rem'
    },
})

